export const num = /^\+?[1-9][0-9]*$/;  //0
export const numZ = /^\+?[0-9][0-9]*$/; //(0)
export const numZ2 = /^-?[0-9][0-9]*$/; //(0)
export const numF = /^-?([0-9][0-9]*)(\.\d{1,2})?$/; //(0,)
export const numF2 = /^([0-9][0-9]*)(\.\d{1,2})?$/; //(,)
export const numD = /^-?([0-9][0-9]*)(\.\d{1,10})?$/; //(0,10)
export const numD2 = /^(([0-9]+[\.]?[0-9]+)|[1-9])$/; //(0,)
export const numD3 = /^(([0-9]+[\.]?[0-9]+)|[0-9])$/; //(,)
export const num0_100 = /^(?:0|[1-9][0-9]?|100)$/;  //0-100
export const num0_1 = /^[01]$|^0\.\d{1,2}$|^1\.0{1,2}$/;  //0-1(),01
export const post = /^\d{6}$/; //
export const phone = /^(((\()?\d{2,4}(\))?[-(\s)*]){0,2})?(\d{7,8})$/; //(2-4,,,1-2;7-8)
export const mobile = /^[1][3,5,8][0-9]{9}$/; //
export const email = /^[0-9a-zA-Z]([-.\w]*[0-9a-zA-Z])*@(([0-9a-zA-Z])+([-\w]*[0-9a-zA-Z])*\.)+[a-zA-Z]{2,9}$/; //Email(@)
export const date = /^\d{4}-\d{2}-\d{2}$/;  //yyyy-MM-dd
export const time = /^\d{2}:\d{2}:\d{2}$/;  //hh:mm:ss
export const datetime = /^\d{4}-\d{2}-\d{2} \d{2}:\d{2}:\d{2}$/;  //yyyy-MM-dd hh:mm:ss
export const datetimeO = /^\d{4}-\d{2}-\d{2}( \d{2}:\d{2}:\d{2})?$/;  //datedatetime
export const pass = /^[0-9a-zA-Z]{6,16}$/;  //,6-16
export const user = /^[a-zA-Z][a-zA-Z0-9_\u4E00-\u9FA5]{3,15}$/;  //,,4-16
export const ip = /^((25[0-5])|(2[0-4]\d)|(1\d\d)|([1-9]\d)|\d)(\.((25[0-5])|(2[0-4]\d)|(1\d\d)|([1-9]\d)|\d)){3}(:(6553[0-5]|655[0-2]\d|65[0-4]\d{2}|6[0-4]\d{3}|[1-5]\d{4}|[1-9]\d{0,3}))?$/; //IPxxx.xxx.xxx.xxxxxx.xxx.xxx.xxx:,xxx0-255,1-65535
export const cardId = /^(\d{18,18}|\d{15,15}|\d{17,17}x)$/i;   //
export const enFirst = /^[a-zA-Z][\s\S]*$/;  //
export const chFirst = /^[\u4E00-\u9FA5][\s\S]*$/;  //
export const percent = /^\+?(([1-9]\d?)|(100)|(0))\%$/;  //(0%-100%)


